@import url(https://fonts.googleapis.com/css2?family=Anton&family=Merriweather:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  width: 100%
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #282c34;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    margin-bottom: 15px;
    -webkit-animation: App-logo-spin infinite 10s linear;
            animation: App-logo-spin infinite 10s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


select:focus, input:focus{
    outline: none;
}

.link {
    text-decoration: none;
    color: rgb(54, 20, 20);
    text-shadow: 0px 0px 1px silver;
}

.navbar {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    background-color: white;
}

.navbar__main {
    width: 65%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar__logo {
    margin-left: 15px
}

.navbar__close {
    display: none;
}

.navbar__menu {
    display: flex;
    padding: 0px;
}

.menu__icon {
    display: none;
}

.menu__item {
    list-style-type: none;
    margin: 0px 30px;  
    transition: font-size .5s;
}

.menu__item:hover {
    font-size: 17.5px;
}

.menu__item a {
    text-decoration: none;
}

.catalogo .main__link {
    padding-bottom: 30px;
}

.catalogo__icon {
    font-size: 18px;
    padding-left: 5px;
    margin-bottom: -5px;
}

.catalogo__dropdown {
    display: none;
    width: 97%;
    color: white;
    position: absolute;
    top: 83px;
    left: 19px;
    grid-template-columns: repeat(4, 1fr);
    background-color: black;
    padding: 8px 0px;
    box-shadow: 0px 2px 5px black;
    z-index: 1
}

.catalogo:hover .catalogo__dropdown {
    display: inline-grid;
}

.cat-dropdown__item {
    list-style-type: none;
    padding: 5px;
    background-color: rgba(99, 178, 49, 0.1);
    border-left: 1px solid #63b231;
}

.cat-dropdown__item a {
    color: white;
    text-decoration: none;
}

.cat-dropdown__item a:hover {
    color: #63b231;
}

.cat-dropdown__item:first-child,
.cat-dropdown__item:nth-child(5),
.cat-dropdown__item:nth-child(9),
.cat-dropdown__item:nth-child(13),
.cat-dropdown__item:nth-child(17),
.cat-dropdown__item:nth-child(21),
.cat-dropdown__item:nth-child(25) {
    border-left: none;
}

.menu__link, .search {
    position: relative;
}

.menu__link:after, .search:after {    
    bottom: -5px;
    content: "";
    display: block;
    height: 3px;
    left: 0%;
    position: absolute;
    background: red;
    transition: width 0.5s ease 0s, left 0.3s ease 0s;
    width: 0;
}

.catalogo .menu__link:after {
    bottom: 25px;
}

.menu__link:hover:after, .search:hover:after { 
    width: 100%; 
    left: 0;
}

.navbar__user {
    display: flex;
    align-items: center;
    margin-right: 20px;
}

.user__tab {
    position: relative;
    padding: 10px;
    border-radius: 10px;
    border: 2px solid black;
    box-shadow: 0px 0px 3px silver;
    background-color: white;
}

.user__arrowicon {
    font-size: 16px;
    padding-left: 5px;
    margin-bottom: -3px;
    color: #63b231
}

.user__menu {
    display: none;
    width: 152px;
    position: absolute;
    top: 40px;
    right: 0px;
    padding: 0px 0px;
    text-align: left;
    margin: 0px;
    box-sizing: border-box;
    background-color: white;
    border-radius: 0px 0px 10px 10px;
    border-left: 3px solid #63b231;
    box-shadow: 0px 0px 3px silver;
}

.usermenu__close {
    display: none
}

.user__menu a {
    text-decoration: none;
}

.user__tab:hover .user__menu {
    display: block
}

.user-menu__item {
    list-style-type: none;
    padding: 8px 10px;
    color: black;
    border-bottom: 1px solid silver
}

 .Carrito .user-menu__item {
    border: none;
    border-radius: 0px 0px 10px 10px;
}

.user-menu__item:hover {
    color: #488523;
    font-weight: 500;
    border-right: 2px solid black;
    box-shadow: 0px 0px 2px silver;
}

.navbar__search {
    width: 97%;
    margin: auto;
    padding: 8px 0px;
    background-color: black;
    border-left: 8px solid #63b231;
    border-right: 8px solid #63b231;
}

.navbar__search--container {
    display: flex;
    width: 45%;
    margin: auto;
    height: 35px;
    border-radius: 30px;
    box-shadow: 0px 0px 2px black;
}

.search__input, .search__select {
    padding: 5px 10px;
    background-color: white;
    border: none;
}
.search__input {
    width: 100%;
    padding: 5px 15px;
    border-right: 1px solid gray;
    border-radius: 30px 0px 0px 30px;
}

.search__button {
    cursor: pointer;
    padding: 5px 10px;
    display: flex;
    justify-content: center;
    background-color: red;
    border-radius: 0px 30px 30px 0px;
    border: 1px solid orangered
}

.search__icon {
    color: white;
    font-size: 25px
}

@media screen and (max-width: 767px) {

    .navbar__main {
        width: 100%;
        background-color: white;
    }
    .navbar__logo {
        margin: 0px;
    }
    .navbar__menu {
        margin: 0;
        padding: 10px 0px;
        position: fixed;
        top: 0;
        left: -100%;
        height: 100vh;
        width: 55%;
        overflow: scroll;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        background-color: black;
        transition: left .9s ease-out;
        border-right: 2px solid #63b231;
        z-index: 3;
    }
    .menu__icon {
        display: flex;
        font-size: 35px;
        margin: 15px 10px;
    }
    a .icon {
        margin: 0px 2px;
        color: black;
    }
    a:hover .icon{
        color: #63b231;
    }
    #navbar__menu:target {
        left: 0;
    }
    #user__tab:target {
        right: 0px;
    }
    .user__tab {
        padding: 10px 0px;
        position: fixed;
        top: 60px;
        right: -100%;
        width: 180px;
        height: 20%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        border: 1px solid #63b231;
        transition: right .9s ease-out;
        z-index: 3;
    }
    .user__tab span {
        padding-left: 10px;
    }
    .usermenu__close {
        display: inline;
        color: red;
        font-size: 10px;
        margin-left: 15px;
    }
    .user__menu {
        width: 101%;
        display: block;
        position: relative;
        top: 10px;
        right: 1px;
        border: 1px solid #63b231;
    }
    .menu__item {
        margin: 10px 0px;
        margin-left: 20px;
        text-align: left;
        color: white;
    }
    .user__cart{
        display: none
    }
    .menu__link, .cat-dropdown__item a {
        padding: 0px;
        text-align: left;
        color: white;
    }
    .catalogo__dropdown {
        margin: 0px;
        margin-top: 5px;
        position: unset;
        width: 100%;
        display: none;
        flex-direction: column;
        padding: 0px;
        top: 0;
        left: 0;
        box-shadow: none;
        grid-template-columns: auto;
        z-index: 0;
    }
    .cat-dropdown__item {
        border: none
    }
    .navbar__close {
        display: flex;
        position: absolute;
        top: 2px;
        right: 5px;
        color: white;
        font-size: 20px;
        border-radius: 50%;
        opacity: 0.4;
    }
    .navbar__search {
        width: 100%;
        border-left: 5px solid #63b231;
        border-right: 5px solid #63b231;
    }
    .navbar__search--container {
        width: 98%;
    }
}

@media screen and (max-width: 320px) {
    .navbar__logo {
        width: 165px;
    }
    .navbar__menu {
        width: 65%;
    }
    .menu__icon {
        font-size: 30px;
    }

    #user__tab:target {
        right: 0px;
    }
    .user__tab {
        padding: 10px 0px;
        position: fixed;
        top: 60px;
        right: -100%;
        width: 60%;
        height: 25%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        border: 1px solid #63b231;
        transition: right .9s ease-out;
    }
    .user__tab span {
        padding-left: 10px;
    }

    .usermenu__close {
        color: red;
        font-size: 10px;
        margin-left: 6px;
    }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
    .navbar {
        display: block;
    }
    .navbar__menu .menu__item {
        margin: 0px 20px;
    }
    .navbar__main {
        width: 100%;
    }
    .catalogo__dropdown {
        left: 12px;
        grid-template-columns: 1.3fr repeat(3, 1fr)
    }
    .user__cart {
        display: none;
    }
    .menu__icon {
        display: flex;
        font-size: 35px;
        margin: 15px;
        order: 1;
    }
    .menu__icon .dropdown {
        display: none;
    }
    a .icon {
        margin: 0px 2px;
        color: black;
    }
    a:hover .icon{
        color: #63b231;
    }
    #user__tab:target {
        right: 0px;
    }
    .user__tab {
        padding: 10px 0px;
        position: fixed;
        top: 60px;
        right: -100%;
        width: 185px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        border: 1px solid #63b231;
        transition: right .9s ease-out;
        z-index: 3;
    }
    .user__tab span {
        padding-left: 10px;
    }
    .usermenu__close {
        display: inline;
        color: red;
        font-size: 10px;
        margin-left: 15px;
    }
    .user__menu {
        width: 101%;
        display: block;
        position: relative;
        top: 10px;
        right: 1px;
        border: 1px solid #63b231;
    }
    .menu__item {
        margin: 10px 0px;
        margin-left: 20px;
        text-align: left;
        color: white;
    }
    .navbar__search--container {
        width: 80%;
    }
}

@media screen and (min-width: 1025px) and (max-width: 1108px) {
    .navbar__menu .menu__item {
        margin: 0px 18px;
    }
}

.user__cart {
    position: relative;
    transition: transform .2s linear;
    margin-right: 10px;
}

.user__cart a {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: black;
}

.user__cart:hover {
    transform: scale(1.05)
}

.cart__icon {
    font-size: 24px;
    padding-left: 5px;
    margin-bottom: -2px;
}

.cart__link {
    display: flex;
    align-items: center;
    padding: 3px 5px;
    padding-left: 0px;
    border-right: 2.5px solid black
}

.cart__counter {
    padding: 3px 5px;
    margin-right: 10px;
}
.item-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 40px 0px;
}
.item__div {
    width: 265px;
    margin: 10px;
    background-color: black;
    border: 1px solid white;
    box-shadow: 0px 0px 5px gray;
    transition: transform ease-out .5s
}

.span__detail {
    display: none;
}

.item__div:hover {
    transform: scale(1.04);
}

.item__div .info__title,
.item__div .info__author,
.item__div .info__price {
    display: block
}

.item__cover {
    width: 225px;
    height: 350px;
    margin-top: 10px;
    margin-bottom: 5px;
    border: 3px solid white;
    box-shadow: 0px 0px 10px gray;
}

.item__info {
    margin-bottom: 10px;
    color: black;
}

.info__title {
    height: 30px;
    overflow: auto;
    font-size: 23px;
    font-weight: bold;
    background-color: white;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 3px solid black;
}

.info__author {
    font-size: 20px;
    background-color: white;
    border-left: 10px solid black;
    border-right: 10px solid black;
    border-bottom: 3px solid black;
}

.info__price {
    font-size: 19px;
    font-weight: 700;
    color: #63b231;
    border-left: 20px solid black;
    border-right: 20px solid black;
    background-color: rgba(99, 178, 49, 0.3);
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
    .item__div {
        margin: 10px 20px;
    }
}

.loading__div {
  margin-top: 50px;
}

.message__title {
    color: white;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    margin-bottom: 15px;
    -webkit-animation: App-logo-spin infinite 10s linear;
            animation: App-logo-spin infinite 10s linear;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.not__found {
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.not__found  h1 {
    color: white;
}

.not__found img {
    width: 30%
}

@media screen and (max-width: 767px) {
    .not__found  h1 {
        font-size: 25px;
    }
    .not__found img {
        width: 250px
    }
}
.scroll__top {
    position: fixed;
    right: 13px;
    bottom: 15px;
    padding: 8px;
    font-size: 50px;
    color: white;
    background-color: black;
    border: 3px solid #63b231;
    border-radius: 100%;
    box-shadow: 0px 0px 2px #63b231;
    -webkit-animation: fadeIn 0.3s;
            animation: fadeIn 0.3s;
}

@-webkit-keyframes fadeIn {
  0% { opacity: 0 }
  100% { opacity: 1 }
}

@keyframes fadeIn {
  0% { opacity: 0 }
  100% { opacity: 1 }
}

@media screen and (max-width: 767px) {
  .scroll__top {
    right: 8px;
  }
}
.item-detail-display {
    min-height: 85vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.item__detail {
    margin: 40px auto;
    width: 80%;
    display: flex;
    background-color: transparent;
    text-align: left;
    border: 2px solid white;
}

.detail__photo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    background-color: rgba(0, 0, 0, 0.2);
}

.detail__photo img {
    width: 240px;
    margin: 10px 20px;
    border: 2px solid white;
    box-shadow: 8px -8px 1px rgba(250, 250, 250, 0.2);
}

.photo__favourite {
    display: flex;
    align-items: center;
    margin-top: 5px;
}

.favourite__add {
    padding: 5px;
    font-size: 18px;
    font-weight: 450;
    margin: 0px;
    border-radius: 5px;
    border: 1px solid red;
    color: white;
    background-color: black;
}

.favourite__added {
    padding: 5px 15px;
    color: white;
    font-size: 18px;
    font-weight: 450;
    margin: 0px;
    border: 1px solid #63b231;
    border-left: none;
    border-radius: 5px;
    background: linear-gradient(279deg, rgba(59,140,8,0.8) 0%, rgba(99,178,49,0.8) 49%, rgba(59,140,8,0.8) 82%)
}

.favourite__icon {
    cursor: pointer;
    padding: 5px;
    font-size: 32px;
    margin-bottom: 1px;
    color: white;
    border-radius: 5px;
    border: 1px solid red;
    background: rgb(210,20,20);
    background: linear-gradient(279deg, rgba(210,20,20,1) 0%, rgba(255,0,12,1) 49%, rgba(210,20,20,1) 82%);
    box-shadow: 0px 0px 5px black;
}

.favourites__link {
    text-decoration: none
}

.detail__info {
    color: white;
    border-left: 2px solid white;
}

.d-info__name, .d-info__author {
    margin: 10px 0px;
    margin-left: 20px;
    color: black;
    background-color: white;
}

.d-info__name {
    margin-top: 10px;
    padding: 2px 10px;
    color: black;
    font-size: 35px;
    letter-spacing: 2px;
    font-family: 'Anton', sans-serif;
    background-color: white;
    box-shadow: -7px 5px 1px rgba(250, 250, 250, 0.3);
}

.d-info__author {
    padding: 0px 30px;
    color: black;
    font-family: 'Merriweather', serif;
    background-color: white
}

.d-info__tags {
    margin: 15px 0px;
    margin-left: 20px;
}

.d-info__tags a {
    text-decoration: none;
    color: inherit;
}

.d-info__synopsis {
    text-align: justify;
    padding: 10px 20px;
    line-height: 20px;
    margin-bottom: 15px;
    background-color: black;
    background-color: rgba(0, 0, 0, 0.35);
    border-top: 1px solid white;
    border-bottom: 1px solid white
} 

.d-info__synopsis .parr:nth-of-type(1)  {
    margin: 0px;
}

.d-info__synopsis .parr {
    display: block;
    margin-top: 10px
}

.d-info__tags span {
    margin: 0px 5px;
    padding: 1px 5px;
    background-color: rgba(128, 128, 128, 0.7);
    background-color: rgba(255, 0, 0, 0.5);
    background-color: rgba(99, 178, 49, 0.7);
    border-radius: 8px;
    border: 1px solid rgba(255, 69, 0, 0.6);
    border: 1px solid #63b231;
}

.detail__footer {
    display: flex;
    background-color: rgba(0, 0, 0, 0.2);
}

.detail__f-tecnica {
    padding-top: 0px;
    width: 50%;
    margin-left: 20px;
    display: inline-block;
    color: white;
}

.detail__f-tecnica h3 {
    padding: 5px 10px;
    margin: 0px 0px;
    margin-bottom: 10px;
    border-bottom: 1px solid white;
    background-color: white;
    color: black;
    box-shadow: -5px 5px 1px rgba(250, 250, 250, 0.3);

}

.detail__f-tecnica p {
    margin: 10px 0px;
    padding: 1px 10px;
    background-color: black;
    background-color: rgba(0, 0, 0, 0.5);
    border-left: 2px solid #63b231;
}

.detail__itemcounter {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-top: 1px solid white;
}

.itemcounter__price {
    margin-top: 10px;
    padding: 2px 20px;
    font-size: 18px;
    background-color: black;
    border: 1px solid #63b231;
    border-radius: 2px;
    box-shadow: 0px 0px 3px #63b231 inset;
}

.price__tag {
    padding-left: 8px;
}

@media screen and (max-width: 767px) {
    .item__detail {
        width: 90%;
    }
    .item__detail, .detail__footer {
        flex-direction: column;
    }
    .detail__photo {
        padding-bottom: 20px;
    }
    .detail__photo img {
        box-shadow: 8px 8px 1px rgb(250 250 250 / 20%);
        margin-bottom: 20px;
    }
    .detail__info {
        border-left: none;
    }
    .d-info__name, .d-info__author {
        margin: 10px 14px;
    }
    .d-info__tags {
        line-height: 27px;
        word-break: break-word;
    }
    .detail__f-tecnica {
        width: 90%;
    }
    .detail__itemcounter {
        width: 100%;
    }
}

@media screen and (max-width: 320px) {
    .item__detail {
        width: 95%;
    }
    .d-info__name {
        font-size: 28px
    }
    .d-info__author {
        padding: 0px 15px;
    }
    
    .d-info__tags span {
        display: block;
        width: 70%;
        padding: 0px 5px;
        margin: 3px 0px;
    }
    .detail__f-tecnica {
        width: 85%;
    }
    .itemcounter__price {
