    }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
    .item__detail {
        width: 90%
    }
    .d-info__author {
        padding: 0px 10px;
    }
    .d-info__tags {
        line-height: 25px;
    }
    .detail__footer {
        flex-direction: column;
    }
    .detail__f-tecnica {
        width: 92%;
        margin-bottom: 5px;
    }
    .detail__itemcounter {
        width: 100%;
        align-self: center;
    }
    .itemcounter__price {
        margin: 10px 5px 0px 5px;
        padding: 2px 5px
